import {distinctUntilChanged, map, multicast, refCount} from 'rxjs/operators';
import {Injectable} from '@angular/core';
import {Observable, ReplaySubject, timer} from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class SecondsTickService {

  private readonly secondsTickers$= timer(0, 150)
  .pipe(
    map(()=> Date.now()),
    distinctUntilChanged((old, current) => {
      return Math.trunc(current/1000) === Math.trunc(old/1000);
    }),
    multicast(() => new ReplaySubject<number>(1)),
    refCount(),
  )

  constructor() { }

  public getSeconds$(): Observable<number> {
    return this.secondsTickers$;
  }
}
