import { Component, OnInit, Input, forwardRef } from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';

@Component({
  standalone: false,
  selector: 'app-prog-input-type-one',
  templateUrl: './prog-input-type-one.component.html',
  styleUrls: ['./prog-input-type-one.component.less'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => ProgInputTypeOneComponent),
      multi: true
    }
  ]
})
export class ProgInputTypeOneComponent implements OnInit, ControlValueAccessor {
  @Input()
  label: string;

  @Input()
  type = 'input';

  private _value: string;
  get value():string {
    return this._value;
  }
  set value(value: string) {
    this.propagateChange(value);
    this._value =value;
  }

  disabled = false;

  propagateChange = (_: any) => {}
  writeValue(value: string): void {
    this.value = value;
  }
  registerOnChange(fn: any): void {
    this.propagateChange = fn;
  }
  registerOnTouched(fn: any): void {
  }
  setDisabledState?(isDisabled: boolean): void {
    this.disabled = isDisabled;
  }

  constructor() { }

  ngOnInit(): void {
  }

  onValueChange(value: string):void {
    this.value = value;
  }

}
